<template>
    <div class="w-100 bg-white custom-chart-div t-min-height Barchrt text-center">
        <loader :isVisible="isLoading"></loader>
        <template v-if="BarWidgetData.length === 0 && isLoading === false">
            <div class="text-danger no-record" :style="{ height: height + 150 + 'px' }"
                style="display: flex; align-items: center; justify-content: center">
                {{ $t("NoRecordfound") }}
            </div>
        </template>
        <template v-else>
            <!-- {{ height }} {{resizeheight * 35}} -->
            <div :id="'BarChart_' + moduleCode + '_' + action.replace(/\s+/g, '_') + index"
                :style="{ height: height + 150 + 'px' }" style="display: grid; align-items: center;">
                <div :class="{
                    'bar d-block mt-2 reportBlock t-min-height align-center c3 w-100': BarWidgetData.length > 0,
                    'bar d-inline-block mt-2 align-center c3 w-100': BarWidgetData.length == 0
                }" :controller="moduleCode" style="display: flex!important;"
                    :id="'div_BarChart_' + action.replace(/\s+/g, '_') + index" :style="{
                        'width:60%; float:left; text-align:center;': BarWidgetData.length > 0,
                        'width:100%;text-align:center;': BarWidgetData.length == 0,
                        'max-height': height + 100 + 'px !important'
                    }">
                </div>
                <!-- <div v-if="isSystemReport == false" class="view-report" @click="ViewReport(reportId)">View Report</div> -->
            </div>
        </template>
    </div>
</template>

<script>
import "d3";
import "c3";
import "c3/c3.min.css";
import DataService from "../../../services/DataService";
export default {
    props: {
        reportName: null,
        chartType: String, // Set the type
        // selectedChartType: String,
        dashboardid: Number,
        moduleCode: null,
        fromDate: "",
        toDate: "",
        filterData: "",
        index: null,
        reportId: null,
        isSystemReport: null,
        width: null,
        height: null,
        resizeheight: null,
    },
    data() {
        return {
            isLoading: true,
            action: this.reportName,
            charT_TYPE: "bar",
            xaxis: "",
            yaxis: "",
            BarWidgetData: [],
            HeaderData: null,
            showAddDashboardReport: false,
            chart: null, // Add a chart property to store the C3.js chart instance
        };
    },
    created() {
        this.FetchData();
    },
    watch: {
        width: 'handleResize',
        height: 'handleResize',
        toDate: 'handleFetchData',
        fromDate: 'handleFetchData',
        filterData: 'handleFetchData',
        chartType: 'handleFetchData',
        resizeheight: 'handleResize',
    },
    methods: {
        ViewReport(reportId) {
            const encodedReportID = btoa(JSON.stringify({ ReportID: reportId }));
            const route = '/Report/viewcustomreport/' + encodedReportID;
            // Open the route in a new tab
            window.open(route, '_blank');
        },
        async FetchData() {
            
            var vueObj = this;
            vueObj.isLoading = true;
            var reportidcheck = vueObj.reportId == undefined ? 0 : vueObj.reportId;
            var params = `moduleName=${vueObj.moduleCode}&currentChartType=${vueObj.charT_TYPE}&dashboardId=${vueObj.dashboardid}&reportName=${vueObj.reportName}&fromDate=${vueObj.fromDate}&toDate=${vueObj.toDate}&ReportId=${reportidcheck}&usersDash=&SearchFilter=${vueObj.filterData}`;
            await DataService.GetDashboardData(params).then(function (response) {
                if (response.data != null && response.data != "") {
                    if (response.data.status == "Success") {
                        if (response.data.result != null) {
                            vueObj.BarWidgetData = response.data.result.data;
                            vueObj.xaxis = response.data.result.Xaxis;
                            vueObj.yaxis = response.data.result.Yaxis;
                            vueObj.bindWidget(vueObj);
                        }
                    }
                }
                vueObj.isLoading = false;
            });
        },
        bindWidget(vueObj) {
            
            var vm = this;
            if (vueObj.charT_TYPE && vueObj.BarWidgetData.length > 0) {
                var xCategory = vueObj.xaxis;
                var yCategory = vueObj.yaxis;
                var arrAU = [];
                var categoryArray = [];
                var multiBars = []
                if (vm.chartType != "multiple_bar") {
                    // eslint-disable-next-line no-undef
                    $.each(vueObj.BarWidgetData, function (index, element) {

                        categoryArray.push(element.NAME);
                        arrAU.push(element.VALUE);
                    });
                    arrAU.splice(0, 0, yCategory);
                }
                else {
                    vm.chartOptionXAxisData = [];
                    var multibarsData = [];
                    var keys = []
                    vueObj.BarWidgetData.forEach((mitem, mainindex) => {
                        ;
                        var obj = {
                            XaxisField: mitem.NAME
                        }
                        categoryArray.push(mitem.NAME);
                        mitem.Data.forEach(item=>{
                            var pushitem = item.YFieldData != undefined ? item.YFieldData : '-';
                            var checkexist = keys.filter(x=> x == pushitem);
                            obj[pushitem] = item.Countable;
                            if(checkexist.length == 0){
                                keys.push(pushitem);
                            }
                        });
                        multibarsData.push(obj);
                    });
                    keys.forEach(function(x) {
                        var obj = {
                            name: x,
                            data:[]
                        };
                        multibarsData.forEach(function(y) {
                            if(y[x]) {
                                obj.data.push(y[x]);
                            } else {
                                obj.data.push(0);
                            }
                        });
                        obj.data.splice(0, 0, x);
                        multiBars.push(obj.data);
                        
                    });
                    // var result = [];
                    // var counts = {};

                    //     // Iterate over jsonData to concatenate Countable values as comma-separated strings
                    //     vueObj.BarWidgetData.forEach(function(item) {
                    //         item.Data.forEach(function(dataItem) {
                    //             if (!counts[dataItem.YFieldData]) {
                    //                 counts[dataItem.YFieldData] = [];
                    //             }
                    //             counts[dataItem.YFieldData].push(dataItem.Countable);
                    //         });
                    //     });

                    //     // Convert counts object to the desired format
                    //     var result = Object.keys(counts).map(function(key) {
                    //         return [key].concat(counts[key]);
                    //     });

                }
                var selector = "#div_BarChart_" + vueObj.action.replace(/\s+/g, '_') + vueObj.index;
                // eslint-disable-next-line no-undef
                $(selector).html("");
                let c3Obj = {
                    bindto: selector,
                    data: {
                        columns: vm.chartType != "multiple_bar" ? [arrAU] : multiBars,
                        type: "bar",
                        empty: {
                            label: {
                                position: "right",
                                text: vueObj.$t("NoRecordfound"),
                            },
                        },
                        // onclick: function() {
                        //     if (this.moduleName == "CRM") {
                        //         window.open("javascript:;");
                        //     } else {
                        //         window.open(
                        //         "/Project/Index?searchCondition=PR.STATUS_ID in(1001)",
                        //         "_blank"
                        //         );
                        //     }
                        // },
                    },
                    tooltip: {
                        grouped: false,
                        position: '',
                    },
                    size: {
                        height: this.height + 100,
                    },
                    bar: {
                        width: {
                            ratio: 0.5,
                        },

                    },
                    legend: {
                        show: true,
                        position: "bottom",
                    },
                    axis: {
                        rotated: (vueObj.chartType.toLowerCase() === "bar" || vueObj.chartType.toLowerCase() === "multiple_bar") ? false : true,
                        x: {
                            label: {
                                text: xCategory,
                                position: "outer-center",
                            },
                            categories: categoryArray,
                            type: "category",
                        },
                        y: {
                            label: {
                                text: yCategory,
                                position: "outer-middle",
                            },
                            padding: {
                                top: 0,
                            },
                        },
                    },
                };
                // eslint-disable-next-line no-undef
                var Barchrt = c3.generate(c3Obj);
                // if(this.resizeheight != null || this.resizeheight != '') {
                //     Barchrt.resize({height: this.resizeheight * 40})
                // }
                // eslint-disable-next-line no-undef
                $(selector).data("c3-chart", Barchrt);
            }
        },
        handleResize() {
            this.bindWidget(this);
        },
        handleFetchData() {
            this.FetchData();
        }
    },
};
</script>
<style scoped>
.view-report {
    margin-left: 10px;
    margin-top: 10px;
    text-decoration: underline;
    color: #1f77b4;
    font-weight: 400;
    cursor: pointer;
    float: left;
}

.chart-container {
    display: inline-block;
    /* Ensure that the container only takes as much width as needed */
    text-align: center;
    /* Center-align the content within the container */
}
</style>
